const state = {
	statusDialogOption: false,
	players: [],
	numberPlayer: 2,
	numberCard: 2,
	numberTrapCard: 1,
};

const getters = {
	getStatusDialogOption: (state) => state.statusDialogOption,
	getPlayers: (state) => state.players,
	getNumberPlayers: (state) => state.numberPlayer,
	getNumberCard: (state) => state.numberCard,
	getNumberTrapCard: (state) => state.numberTrapCard,
};

const actions = {
	setStatusDialogOption({ commit }, data){
		commit('SET_STATUS_DIALOG_OPTION', data)
	},
	setOption({ dispatch }, data){
		// dispatch('setPlayers', data.selectedLeague)
		dispatch('setNumberPlayer', data.numberPlayer)
		dispatch('setNumberCard', data.numberCard)
		dispatch('setNumberTrapCard', data.numberTrapCard)
		dispatch('setIsStart', false, { root: true }) // -> 'someOtherAction
	},
	setPlayers({commit}, data){
		commit('SET_PLAYERS', data)
	},
	setNumberPlayer({ commit }, data){
		commit('SET_NUMBER_PLAYER', data)
	},
	setNumberCard({ commit }, data){
		commit('SET_NUMBER_CARD', data)
	},
	setNumberTrapCard({ commit }, data){
		commit('SET_NUMBER_TRAP_CARD', data)
	},
};

const mutations = {
	SET_STATUS_DIALOG_OPTION: (state, data) => state.statusDialogOption = data,
	SET_PLAYERS: (state, data) => state.players = data,
	SET_NUMBER_PLAYER: (state, data) => state.numberPlayer = data,
	SET_NUMBER_CARD: (state, data) => state.numberCard = data,
	SET_NUMBER_TRAP_CARD: (state, data) => state.numberTrapCard = data,
};

export default {
	state,
	getters,
	actions,
	mutations
}