const state = {
    statusDialogPlayground: false, 
    isStart: false,
    dataCards: [],
};

const getters = {
    getStatusDialogPlayground: (state) => state.statusDialogPlayground, 
    getIsStart: (state) => state.isStart,
    getDataCards: (state) => state.dataCards,
};

const actions = {
    setStatusDialogPlayground({ commit }, data){
        commit('SET_STATUS_DIALOG_PLAYGROUND', data)
    }, 
    setIsStart({ dispatch, commit }, data){
        dispatch('setDataCard', data)
        commit('SET_IS_START', data)
    },
    setDataCard({ commit, rootGetters }){
        let numCard = rootGetters.getNumberCard;
        let numTrapCard = rootGetters.getNumberTrapCard;

        // membuat urutan kartu nya
        let OrderNumCard = [];
        for (var i = 1; i <= numCard; i++) {
            OrderNumCard.push(i)
        }
        // console.log('urutan = '+OrderNumCard)

        // membuat acak penerima kartu
        // ----------------------------------------------------------------------------
        function shuffle(array) {
            var currentIndex = array.length, temporaryValue, randomIndex;

            // While there remain elements to shuffle...
            while (0 !== currentIndex) {

                // Pick a remaining element...
                randomIndex = Math.floor(Math.random() * currentIndex);
                currentIndex -= 1;

                // And swap it with the current element.
                temporaryValue = array[currentIndex];
                array[currentIndex] = array[randomIndex];
                array[randomIndex] = temporaryValue;
            }

            return array;
        }
        let ramdomNumCard = shuffle(OrderNumCard);
        // ----------------------------------------------------------------------------
        
        // pembagian kartu
        let mainCard = [];
        for (var q = 1; q <= numCard; q++) {
            mainCard.push({
                index: q,
                value: ramdomNumCard[q-1],
                hasil: ramdomNumCard[q-1] <= numTrapCard ? 'mdi-close' : 'mdi-checkbox-blank-circle-outline',
                isFlip: false
            });
        }
        
        // console.log('acak = '+ramdomNumCard)
        // console.log('hasil = '+mainCard)
        commit('SET_DATA_CARDS', mainCard)
    },
    setFlip({ commit }, data){
        commit('SET_FLIP', data)
    }
};

const mutations = {
    SET_STATUS_DIALOG_PLAYGROUND: (state, data) => state.statusDialogPlayground = data, 
    SET_IS_START: (state, data) => state.isStart = data,
    SET_DATA_CARDS: (state, data) => state.dataCards = data,
    SET_FLIP: (state, data) => state.dataCards[data].isFlip = true,
};

export default {
    state,
    getters,
    actions,
    mutations
}